<template>
	<div class="page4_1">
		<div class="nav">
			<ul class="fl">
				<li>
					<router-link active-class="active" class="item" :to="{path:'/inside/page4_1'}">
						报告准备
					</router-link>
				</li>
			</ul>
			<!-- <div class="fr inter">
				<interBtn title="报告要素" id="more4_1"/>
			</div> -->
		</div>
		<div class="content_page">
			<div class="page-top">
				<Place class="fl" :data="msg"/>
				<div class="fr inter">
					<interBtn title="报告要素" id="more4_1" ydname="审计报告自动生成"/>
				</div>
			</div>
			<div>
				<div class="box box-border-shadow">
					<el-steps :active="active">
					    <el-step title="报告要素"></el-step>
					    <el-step title="审计结论及建议"></el-step>
					    <el-step title="审计法规及结论"></el-step>
					    <el-step title="报告内容"></el-step>
					</el-steps>
					<div style="overflow: hidden;margin-bottom: 20px;">
						<el-button :disabled="active==1" size="mini" round style="margin-top: 12px;float: left;" @click="prev">上一步</el-button>
						<el-button :disabled="active==4" size="mini" round style="margin-top: 12px;float: right;" @click="next">下一步</el-button>
					</div>
					<div v-show="active===1">
						<div class="text tishi-word">温馨提示：选择要生成的报告所包含的要素（多选）</div>
						<div style="margin-top: 20px;border-top:1px solid #ccc;padding-top: 20px;box-sizing: border-box;">
							<el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
							<el-checkbox v-model="checkMr" @change="handleCheckMrChange">恢复默认</el-checkbox>
							<el-radio-group v-model="ysType" style="margin-left: 40px;">
						      <el-radio label="社会审计"></el-radio>
						      <el-radio label="政府审计"></el-radio>
						    </el-radio-group>
							<div style="margin: 15px 0;"></div>
							<el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
								<div class="item" v-for="city in cities" :class="'indent'+((city.split('.')).length-1)">
								   <el-checkbox :label="city" :key="city">{{city}}</el-checkbox>
								</div>
							</el-checkbox-group>
						</div>
					</div>
					<div v-show="active===2">
				    	<div v-if="tableData.length>0">
					    	<el-table
						    ref="multipleTable"
						    :data="tableData"
						    tooltip-effect="dark"
						    style="width: 100%"
						    @selection-change="handleSelectionChange">
							    <el-table-column
							      type="selection"
							      width="55">
							    </el-table-column>
							    <el-table-column
							      prop="审计疑点"
							      label="审计疑点"
							      width="160">
							    </el-table-column>
							    <el-table-column
							      prop="审计结论"
							      label="审计结论"
							      show-overflow-tooltip
							     >
							    </el-table-column>
							    <el-table-column
							      prop="审计建议"
							      label="审计建议"
							      show-overflow-tooltip
							     >
							    </el-table-column>
							</el-table>
					    </div>
					    <div v-else>
					    	<div style="line-height: 100px;height: 100px;text-align: center;font-size: 20px;color: #b80000">
								<i class="am-icon-spinner am-icon-spin"></i> loading
							</div>
					    </div>
					</div>
					<div v-show="active===3">
				    	<div v-if="tableData1.length>0">
					    	<el-table
						    ref="multipleTable1"
						    :data="tableData1"
						    tooltip-effect="dark"
						    style="width: 100%"
						    @selection-change="handleSelectionChange1">
							    <el-table-column
							      type="selection"
							      width="55">
							    </el-table-column>
							    <el-table-column
							      prop="审计疑点"
							      label="审计疑点"
							      width="160">
							    </el-table-column>
							    <el-table-column
							      prop="审计结论"
							      label="审计结论"
							      show-overflow-tooltip
							     >
							    </el-table-column>
							    <el-table-column
							      prop="审计法规"
							      label="审计法规"
							      show-overflow-tooltip
							     >
							    </el-table-column>
							</el-table>
					    </div>
					    <div v-else>
					    	<div style="line-height: 100px;height: 100px;text-align: center;font-size: 20px;color: #b80000">
								<i class="am-icon-spinner am-icon-spin"></i> loading
							</div>
					    </div>
					</div>
					<div v-show="active===4">
						<el-form :rules="rules" status-icon ref="form1" style="width: 60%;margin: 0 auto" label-position="right" label-width="120px" :model="form1">
						    <el-form-item label="报告名称:" prop="title">
						       <el-input v-model="form1.title" placeholder="请输入报告名称"></el-input>
						    </el-form-item>
						    <el-form-item label="报告作者:" prop="author">
						   	   <el-input v-model="form1.author" placeholder="请输入报告作者"></el-input>
						    </el-form-item>
						    <el-form-item label="收件人:" prop="name">
						   	   <el-input v-model="form1.name" placeholder="请输入收件人"></el-input>
						    </el-form-item>
						    <el-form-item label="报告索引号:" prop="num">
						   	   <el-input v-model.number="form1.num" placeholder="请输入报告索引号"></el-input>
						    </el-form-item>
						    <!-- <el-form-item label="审计疑点选择:" prop="type">
						    	<el-select v-model="form1.type" placeholder="请选择要生成的审计报告疑点" style="display: block">
							       <el-option v-for="item in getWords" :label="item.name" :value="item.name"></el-option>
							    </el-select>
						    </el-form-item> -->
						    
						    <el-form-item>
							    <el-button type="primary" @click="submitForm('form1')">初稿生成</el-button>
							     <el-button @click="resetForm('form1')">重置</el-button>
							</el-form-item>
						</el-form>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Place from './page/place'
	import reqData from "../../reqData.js"
	import interBtn from './interactions'
	// var checkedMr = ['1 报告标题', '2 报告索引号', '3 收件人', '13 注册会计师名称、地址、签字、盖章','14 报告日期'];
	export default{
		name:'page4_1',
		components:{
			interBtn,
			Place
		},
		data(){
			var checkNum = (rule, value, callback) => {
		        if (!value) {
		          return callback(new Error('请输入报告索引号'));
		        }
		        setTimeout(() => {
		          if (!Number.isInteger(value)) {
		            callback(new Error('请输入数字值'));
		          } else {
		            callback();
		          }
		        }, 1000);
		    };
			return{
				msg:["审计报告自动生成","报告准备"],
				checkAll: false,
				checkedMr:[],
		        checkedCities: [],
		        cities: [],
		        checkMr:true,
		        isIndeterminate: true,
		        ysType:"社会审计",
				form1: {
		          title:'',
		          author: '',
		          name:'',
		          num:'',
		          type:''
		        },
		        rules: {
		          title: [
		            { required: true, message: '请输入报告名称', trigger: 'blur' },
		            // { min: 3, message: '长度大于3个字符', trigger: 'blur' }
		          ],
		          author: [
		            { required: true, message: '请输入报告作者', trigger: 'blur' }
		          ],
		          name: [
		            { required: true, message: '请输入收件人', trigger: 'blur' }
		          ],
		          type: [
		            { required: true, message: '请选择要生成的审计报告疑点', trigger: 'change' }
		          ],
		          num: [
		            { required: true,validator: checkNum, trigger: 'blur' }
		          ]
		        },
		        tableData:[],
		        multipleSelection: [],
		        active: 1,
		        tableData1:[],
		        multipleSelection1: [],
			}
		},
		watch:{
			checkedCities:function(val) {
				// sessionStorage.setItem("baogaoyaosu",JSON.stringify(val)); 
			}
		},
		computed:{
			getFormData(){
				console.log(JSON.parse(sessionStorage.getItem("baogaoForm")))
				return JSON.parse(sessionStorage.getItem("baogaoForm"));
			},
			getWords(){
				return JSON.parse(sessionStorage.getItem("WordCloud"));
			},
			getWord(){
				return this.$store.getters.getWord||sessionStorage.getItem("word");
			},
		},
		methods: {
			handleSelectionChange(val) {
		       this.multipleSelection = val;
		    },
		    handleSelectionChange1(val) {
		       this.multipleSelection1 = val;
		    },
	    	submitForm(formName) {
		        this.$refs[formName].validate((valid) => {
		          if (valid) {
		          	let formData={
		          		title:this.form1.title,
		          		name:this.form1.name,
		          		author:this.form1.author,
		          		num:this.form1.num,
		          		type:this.getWord
		          	};
		          	console.log(formData);
		            sessionStorage.setItem("baogaoForm",JSON.stringify(formData)); 
		            sessionStorage.setItem("baogaoyaosu",JSON.stringify(this.checkedCities)); 
		            sessionStorage.setItem("AuditDoubt",JSON.stringify(this.multipleSelection)); 
		            sessionStorage.setItem("AuditDoubt_fagui",JSON.stringify(this.multipleSelection1));  
	        		this.$router.push({ path: 'page4_3',params: { form: JSON.stringify(formData) }});
		          } else {
		            console.log('error submit!!');
		            return false;
		          }
		        });
		    },
		    resetForm(formName) {
		        this.$refs[formName].resetFields();
		    },
		    dataSelects(){
		    	reqData
			        .req({
			          apiName: "yaosu"
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            let data_moren = res.data_moren;
			            console.log(data);
			            let arr=[];
			            let arr2=[];
			            data.forEach((v)=>{
			            	arr.push(v.id+" "+v.yaosu);
			            });
			            data_moren.forEach((v)=>{
			            	arr2.push(v.id+" "+v.yaosu);
			            });
			            this.cities= arr;
			            this.checkedMr= arr2;
			            this.checkedCities=arr2;
			            console.log(this.checkedMr);
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
		    	reqData
		            .req({
		                apiName: "AuditDoubt",
		                data: {             
			                word: this.getWord
			            }
		            })
		            .then(res => {
		                res = res.data;
		                if (res && res.code === "200") {
		                	let data=res.data;
		                	console.log(data);
		                	this.tableData=data
		                }
		            })
		            .catch(err => {
		            	console.log(err);
		            });
		        reqData
		            .req({
		                apiName: "AuditDoubt_fagui",
		                data: {             
			                word: this.getWord
			            }
		            })
		            .then(res => {
		                res = res.data;
		                if (res && res.code === "200") {
		                	let data=res.data;
		                	console.log(data);
		                	this.tableData1=data
		                }
		            })
		            .catch(err => {
		            	console.log(err);
		            });
		    },
		    prev(){
		    	if (this.active-- < 2) this.active = 1;
		    },
			next() {
			    if (this.active++ > 3) this.active = 4;
			},
		    handleCheckAllChange(val) {
		        this.checkedCities = val ? this.cities : [];
		        this.checkMr = (this.checkedCities === this.checkedMr);
		        this.isIndeterminate = false;
	        },
	        handleCheckedCitiesChange(value) {
		        let checkedCount = value.length;
		        this.checkMr = (value === this.checkedMr);
		        this.checkAll = checkedCount === this.cities.length;
		        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
	        },
	        handleCheckMrChange(val){
		      	this.checkAll = false;
		      	if(val){
		      		this.checkedCities = this.checkedMr;
		      	}
	        }
	    },
	    mounted(){
	    	this.dataSelects();
	    }
	}
</script>
<style type="text/css" scoped="">
	.page4_1 .inter{
		height: 44px;
		line-height: 44px;
	}
	.page4_1 .box{
		min-height: 600px;
    	text-align: center; 
    	margin: 20px auto;
        width: 100%;
        padding: 30px 60px;
        box-sizing: border-box;
        text-align: justify;
        border-radius: 8px;
        background-color: #fff;
	}
	.item{
		display: block;
		width: 100%;
		box-sizing: border-box;
		line-height: 30px;
	}
	.indent1{
		padding-left: 10px;
	}
	.indent2{
		padding-left: 20px;
	}
	.indent3{
		padding-left: 30px;
	}
	.indent4{
		padding-left: 40px;
	}
	.el-checkbox {
	    display: inline-block;
	}
</style>


